.profile__name {
  margin: 0;
  font-weight: 500;
  font-size: 42px;
  line-height: 1.14;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}


@media (max-width: 650px) {
  .profile__name {
    grid-column-start: 2;
    font-size: 27px;
    line-height: 1.22;
  }
}
